import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {ListPropertiesComponent} from './pages/list-properties/list-properties.component';
import {CreatePropertyComponent} from './pages/create-property/create-property.component';

const routes: Routes = [
  { path: 'manage', component: CreatePropertyComponent},
  { path: '', component: ListPropertiesComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
